<template>
    <div class="list">
        <a class="item with-avatar" v-for="shop in shopList">
            <div class="avatar avatar-xl"><img v-bind:src="shop.mtzp + '?x-oss-process=image/resize,m_lfit,w_80,limit_0/auto-orient,0/sharpen,93/quality,q_100'" v-bind:alt="shop.zp_name"></div>
            <div class="content">
                <div class="title strong">{{shop.zp_name}}</div>
                <div class="subtitle">食品安全等级：{{shop.dj}}</div>
                <div>
                    <span class="distance text-gray small">距离{{shop.dis}}</span>
                    <span class="pull-right text-gray small">{{shop.sq}}</span>
                </div>
            </div>
        </a>
    </div>
</template>
<script>
    import Vue from 'vue'
    import { getShopList } from "../api/api"
    export default {
        name:'favorite',
        data() {
            return {
                shopList: [],
            }
        },
        mounted() {
            getShopList().then(response => {
                this.shopList = response.data
            })
        }
    }
</script>
<style lang="scss">

</style>


