<template>
  <div class="warp">
    <div class="search-tool">
      <input name="zp_name" type="text" class="input" placeholder="请输入商家名称或商圈">
    </div>
    <div class="shop-content">
      <div class="filter container" data-spm="filter">
        <!-- <div class="filter-bar row">
          <span class="bar-item cell" @click="filterClick(1)" v-bind:class="{ 'text-accent': activeFilter==1 }">菜系&nbsp;&nbsp;<i class="icon icon-chevron-down"></i></span>
          <span class="bar-item cell" @click="filterClick(2)" v-bind:class="{ 'text-accent': activeFilter==2 }">排序&nbsp;&nbsp;<i class="icon icon-chevron-down"></i></span>
          <span class="bar-item cell" @click="filterClick(3)" v-bind:class="{ 'text-accent': activeFilter==3 }">安全等级&nbsp;&nbsp;<i class="icon icon-chevron-down"></i></span>
        </div> -->
        <div class="row tiles">
          <div class="cell" v-bind:class="{ 'text-accent': activeFilter==1 }"><div class="btn rimary-pale" @click="filterClick(1)">菜系<i class="icon icon-chevron-down"></i></div></div>
          <div class="cell" v-bind:class="{ 'text-accent': activeFilter==2 }"><div class="btn rimary-pale" @click="filterClick(2)">排序<i class="icon icon-chevron-down"></i></div></div>
          <div class="cell" v-bind:class="{ 'text-accent': activeFilter==3 }"><div class="btn rimary-pale" @click="filterClick(3)">安全等级<i class="icon icon-chevron-down"></i></div></div>
        </div>
        <div class="expanding" v-show="expandingShow">
          <div class="sort list with-divider">
            <span class="sort-item item">默认</span>
            <span class="sort-item item">离我最近</span>
            <span class="sort-item item">最新发布</span>
          </div>
        </div>
        <div class="mask" v-show="expandingShow" @click="expandingHide"></div>
      </div>
      <scroller :on-refresh="refresh" :on-infinite="infinite">
        <div class="list with-divider shop-list">
            <a class="item with-avatar" v-for="shop in shopList" @click="toShopDetail(shop.id)">
                <div class="avatar avatar-xl"><img v-bind:src="shop.mtzp + '?x-oss-process=image/resize,m_lfit,w_80,limit_0/auto-orient,0/sharpen,93/quality,q_100'" v-bind:alt="shop.zp_name"></div>
                <div class="content">
                    <div class="title strong">{{shop.zp_name}}</div>
                    <div class="subtitle">食品安全等级：{{shop.dj}}</div>
                    <div>
                        <span class="distance text-gray small">距离{{shop.dis}}</span>
                        <span class="pull-right text-gray small">{{shop.sq}}</span>
                    </div>
                </div>
            </a>
        </div>
      </scroller>
    </div>

  </div>
</template>

<script>
  import { getShopList } from "../api/api"
  import Vue from 'vue'
  import VueScroller from 'vue-scroller'
  Vue.use(VueScroller)

  export default {
    name: 'Home',
    data() {
      return {
        shopList: [],
        expandingShow: false,
        activeFilter: 0,
      }
    },
    methods: {
      refresh(done) {
        getShopList().then(response => {
          this.shopList = response.data
          done()
        })
      },
      infinite(done) {
        getShopList().then(response => {
          this.shopList = this.shopList.concat(response.data)
          done()
        })
      },
      filterClick(index){
        this.expandingShow = !this.expandingShow
        if(this.expandingShow)
          this.activeFilter = index
        else
          this.activeFilter = 0
      },
      expandingHide(){
        this.expandingShow = false
        this.activeFilter = 0
      },
      toShopDetail(id){
        // console.info('this.$router',this.$router)
        this.$router.push({
          path: `/shopDetail/${id}`,
        })
        // console.info('this.$routerss',this.$router)
      }
    },
    components: {

    },
    mounted() {
      getShopList().then(response => {
        this.shopList = response.data
      })
    }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
  .search-tool{
    width: 100%;
    top: 0;
    z-index: 99999;
    position: absolute;
    background-color: #fff;
    .input{
      height: 2rem;
    }
  }

  .shop-content{
    width: 100%;
    height: 100%;
  }

  .filter{
    width: 100%;
    top: 2rem;
    z-index: 99999;
    position: absolute;
    background-color: #fff;
    .filter-bar{
      width: 100%;
      cursor: pointer;
      font-size: .32rem;
      z-index: 2;
      background-color: #fff;
      line-height: 1.5rem;
      .cell{
        display: block;
        line-height: 1.5rem;
      }
    }
    .row{
      margin: 0;
    }
    .mask {
        position: fixed;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0,0,0,.5);
        z-index: 2;
    }
  }
  .shop-list{
    padding-top: 4rem;
    background-color: white;
  }
</style>



