<template>
  <div class="shop-warp">
    <!-- <div class="shop-pic">
      <img v-bind:src="shop.mtzp" v-bind:alt="shop.zp_name">
    </div>         -->
    <div class="container">
         <div class="shop-content-list-item row">
            <div class="icon-part pin">
                <img class="shop-logo" v-bind:alt="shop.zp_name" v-bind:src="shop.mtzp + '?x-oss-process=image/resize,m_lfit,w_80,limit_0/auto-orient,0/sharpen,93/quality,q_100'"
                    data-src-checked="true">
            </div>

            <div class="right">
                <div class="title-wrap">
                <span class="title">{{shop.zp_name}}</span>
                </div>
                <!-- <div class="delivery">
                    <span class="distance text-gray small">距离{{shop.dis}}</span>
                    <span class="pull-right text-gray small">{{shop.sq}}</span>
                </div> -->
                <br>
                <div>
                    <button type="button" class="btn warning btn-sm outline pull-right">
                        <i class="icon icon-star">收藏</i>
                    </button>
                </div>
            </div>
        </div>
    </div>
    <section>
      <div class="list with-divider space">
          <div class="heading">
            <div class="title">商家信息</div>
          </div>
          <a class="item">
                <span class="title">食品安全等级：</span>
                <i class="icon icon-smile muted">良好</i>
            </a>
            <a class="item">
                <span class="title">电话:01066023102</span>
                <i class="icon icon-phone-sign muted">联系商家</i>
            </a>
            <a class="item">
                <span class="title">地址:嘉兴市斜西街22号</span>
            </a>

            <div class="heading">
                <div class="title">检查记录</div>
            </div>
           <a class="item">
                <span class="title">检查图片</span>
                <i class="icon icon-chevron-right muted"></i>
            </a>
            <a class="item">
                <span class="title">历次检查</span>
                <i class="icon icon-chevron-right muted"></i>
            </a>
            <a class="item">
                <span class="title">从业人员</span>
                <i class="icon icon-chevron-right muted"></i>
            </a>
        </div>
    </section>
  </div>
</template>
<script>
  import Vue from 'vue'
  import { getShopList} from "../api/api"
  export default {
    name: 'shopDetail',
    data() {
      return {
        shop: ''
      }
    },
    mounted() {
      let id = this.$route.params.id
      getShopList().then(response => {
        let self = this
        response.data.forEach(function (each) {
          if (each.id == id)
            self.shop = each
        })

        // //更新title
        // this.$route.meta.title = this.shop.zp_name
      })
    }
  }

</script>
<style lang="scss">
.shop-warp{
    background-color: white;
    padding-top: 1rem;
    box-shadow: rgba(0,0,0,.05) 0 0.05rem 0.2rem, rgba(0,0,0,.12) 0 0.05rem 0.1rem;
//   .shop-pic {
//     height: 10rem;
//     overflow: hidden;
//   }
    .right{
        text-align: left;
        margin-left: .5rem;
        display: flex;
        flex: 1;
        -webkit-box-flex: 1;
        width: 100%;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
        -ms-flex-direction: column;
        flex-direction: column;
        -webkit-box-align: start;
        .title{
        font-size: 16px;
        font-weight: 700;
        }
    }
}


</style>



